import "./topbar.scss";
import { Person, Mail, Instagram, GitHub, LinkedIn } from "@material-ui/icons";

const Topbar = ({menuOpen,setMenuOpen}) => {
  return (
    <div className={"topbar " +(menuOpen && "active")}>
      <div className="wrapper">
        <div className="left">
          <a href="#intro" className="logo">
            Ahmet<span>adm.</span>{" "}
          </a>

          <div className="itemContainer">
            <a href="http://instagram.com/ahmetadm" target="_blank">
              <Instagram className="s-icon" />
            </a>
          </div>
          <div className="itemContainer">
            <a href="https://linkedin.com/in/ahmetadem" target="_blank">
              <LinkedIn className="s-icon" />
            </a>
          </div>
          <div className="itemContainer">
            <a href="https://github.com/ahmetadm" target="_blank">
              <GitHub className="s-icon" />
            </a>
          </div>

          <div className="itemContainer cinfo">
            <Person className="icon" />
            <a href="tel:+38972321268">+389 72 321 268</a>
          </div>
          <div className="itemContainer cinfo">
            <Mail className="icon" />
            <a href="mailto:ahmet.a.adem@gmail.com" target="_blank">ahmetaadm@gmail.com</a>
          </div>
        </div>

        <div className="right">
            <div className="hamburger" onClick={()=>setMenuOpen(!menuOpen)}>
                <span className="line1"></span>
                <span className="line2"></span>
                <span className="line3"></span>

            </div>
        </div>
      </div>
    </div>
  );
};

export default Topbar;
